/* App CSS */
.App {
  text-align: center;
}

.App-logo {
  height: 4vmin;
  pointer-events: none;
}

.react-logo {
  height: 4vmin;
  pointer-events: none;
}

.navbar-custom {
    background-color: "#229ed4";
}

.App-Navigation {
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: right;
  color: white;
  vertical-align: top;
}

.App-Navigation .nav-item a {
  color: white;
  font-weight: 600;
}

.App-link {
  color: #61dafb;
}

.lead {
  padding: 1rem 0;
}

section {
  padding: 5rem 0;
}

.router {
  padding-bottom: 55px;
}

a:hover {
  text-decoration: none;
}

#landing {
  max-width: 60%;
  margin: auto;
}

.white {
  color: white;
  text-align: center;
  margin: auto;
}
.red {
  background-color: #f22613;
}

.cernBlue {
  color: #0194d3;
}

.notify {
  color: red;
  font-weight: 300;
  padding-top: 7.5px;
  padding-left: 5px;
}

.right {
  padding-left: 25px;
}

.red {
  color: red;
  background-color: rgba(0, 0, 0, 0);
}
.green {
  color: green;
}

.logo {
  width: 500px;
}

.black {
  color: black;
}

.fixed-background {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #111214;
  background-image: url("images/bg.jpg");
  background-size: auto;
  background-position: center -263px;
  z-index: -1;
}

#main-nav {
  transition: background-color 0.3s ease;
}

#main-nav.scrolled {
  background-color: rgba(17, 18, 20, 0.92);
  backdrop-filter: blur(50px) saturate(2);
}

.navbar.transparent.navbar-inverse .navbar-inner {
  border-width: 0px;
  -webkit-box-shadow: 0px 0px;
  box-shadow: 0px 0px;
  background-color: rgba(0, 0, 0, 0);
  background-image: -webkit-gradient(
    linear,
    50% 0%,
    50% 100%,
    color-stop(0%, rgba(0, 0, 0, 0)),
    color-stop(100%, rgba(168, 121, 121, 0))
  );
  background-image: -webkit-linear-gradient(
    270deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}

.navbar-default .navbar-brand {
  color: #229ed4;
}

.gray {
  background-color: #111214;
}
@keyframes moveup {
  10% {
    transform: translate(0px, 0px);
  }
  50% {
    transform: translate(15px, -25px);
  }
  90% {
    transform: translate(0px, 0px);
  }
}
@keyframes moveup2 {
  10% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(35px, -40px);
  }
  80% {
    transform: translate(0, 0);
  }
}
@keyframes moveup3 {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(70px, -80px);
  }
  90% {
    transform: translate(0, 0);
  }
}
